* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
article {
    display: flex;
    section {
        height: 100px;
        background-color: red;
    }
}
.list {
    list-style: none;
    display: flex;
    justify-content: center;
}
.circles {
    display: flex;
    justify-content: center;
    flex-shrink: 0;
    flex-wrap: wrap;
}

.circle {
    margin: 10px;
    text-align: center;
    font-size: 40px;
    color: white;
    width: 15vw;
    height: 30vh;
    background-color: red;
    border-radius: 50%;
}

// zad 3

.crazy-layout {
    font-size: 30px;
    color: black;
    display: flex;
    flex-wrap: wrap;
}
.box {
    height: 100px;
    width: 100%;
}
div[class*="box"] {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    background-color: aqua;
}
div[class*="row"] {
    padding: 10px;
}
.row100 {
    width: 100%;
}
.row50 {
    width: 50%;
}
.row25 {
    width: 25%;
}
.row70 {
    width: 70%;
}
.row30 {
    width: 30%;
}
@media (max-width: 420px) {
    .row30, .row70, .row50, .row100 {
        width: 100%;
    }
    .row25 {
        width: 50%
    }
    .crazy-layout div:nth-child(10) {
        order: -1;
    }
}
